"use client";

import WSButton from "@/app/UI/WSButton/WSButton";
import { Button } from "@/components/ui/button";
import { Avatar, AvatarFallback, AvatarImage } from "@radix-ui/react-avatar";
import { ArrowRight, ArrowUpRight, Circle, Link } from "lucide-react";

import { motion } from "framer-motion";
import { Fragment, useEffect, useState } from "react";
import ServiceCard from "@/app/components/ServiceCard";
logo
const { list1, list2 } = Logos[0];


export default function Hero() {

// state for logo switch
let [activeIndex, setActiveIndex] = useState('0')

  const [animateMobile, setAnimateMobile] = useState("");
  useEffect(() => {
    const handleResize = () => {
      setAnimateMobile(window.innerWidth <= 768);
    };

    handleResize();
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  return (
    <Fragment>
      {/* hero */}
      <section className=" flex flex-col px-[5%] border items-center justify-center bg-white pb-[20px] md:px-12">
        <div className="  w-full h-[100vh] grid md:grid-cols-2  items-center">
          {/* Left Text Content */}
          <div className=" ">
            <h1 className="text-4xl md:text-6xl font-bold leading-tight text-gray-900 ">
              We craft fast, scalable sites for teams of all sizes.
            </h1>

            <h4 className="font-normal text-[20px] leading-[30px] ">
              Webstacks empowers marketing teams to break free from website
              bottlenecks with a composable approach built for speed and scale.
            </h4>

            <div className="flex gap-4">
              <WSButton icon={<ArrowUpRight />} value="Get Started">
                Get Started
              </WSButton>
              <Button className="px-6 py-6 text-base flex items-center gap-2">
                View Client Stories
                <ArrowRight className="w-5 h-5 text-white" />
              </Button>
            </div>
          </div>

          {/* Right Image */}
          <div className=" border-1  rounded-[10px] h-[80%]">
            <div className=" flex flex-row text-[10px] h-[36px] border-b-1 justify-between items-center px-4 rounded-t-sm">
              <div className="flex flex-row space-x-1">
                <Circle className="w-4 h-4 bg-gray-200 text-gray-200 rounded-[10px]" />
                <Circle className="w-4 h-4 bg-gray-200 text-gray-200 rounded-[10px]" />
                <Circle className="w-4 h-4 bg-gray-200 text-gray-200 rounded-[10px]" />
              </div>
              <div className="flex justify-center   w-full">
                <Link />
              </div>
            </div>
            <div className="overflow-hidden w-full flex flex-col justify-center  scrollbar-hide">
              <Avatar className="h-150">
                <motion.img
                  src="/images/heroScrolls/scroll-1.jpg"
                  alt="Scrolling image"
                  className="w-full h-auto"
                  initial={{ y: 0 }}
                  animate={animateMobile ? { y: -1400 } : { y: -2400 }}
                  transition={{
                    duration: 10,
                    ease: "easeInOut",
                    repeat: Infinity,
                    repeatType: "reverse",
                  }}
                />
              </Avatar>
            </div>
          </div>
        </div>
        <div className="item-start   w-full border-b-1 pb-[10px]">
          <h4 className="text-[20px]">
            Trusted by the world's fastest-growing{" "}
            <span onClick={()=>{setActiveIndex('0')}} className="font-semibold text-primary cursor-pointer">
              Startups
            </span>{" "}
            and{" "}
            <span onClick={()=>{setActiveIndex('1')}}  className="font-semibold text-primary cursor-pointer">
              Enterprises
            </span>
          </h4>
        </div>
        <div className="flex flex-row justify-between bg-gray-200 w-full">
          {[...list1, ...list2].map((item, index) => {
            activeIndex = index
            return (
              <div key={index} >
                <Avatar>
                  <AvatarImage src={item[0].list1.logo}  className=" w-20 h-20 object-contain grayscale" />
                  <AvatarFallback>Logo here</AvatarFallback>
                </Avatar>
              </div>
            );
          })}
        </div>
      </section>

      {/* Services */}
      <section className="  flex flex-col items-center justify-center bg-black ">
        <div className="container  flex justify-center pt-[110px]">
          <div className="upper text-center w-[72%]  space-y-8">
            <h2 className="text-[60px]  md:text-6xl text-center font-bold leading-tight text-white">
              Websites aren’t just part of what we do. They’re everything we do.
            </h2>
            <p className="text-[#a5acc0] text-center text-[20px] leading-[30px]">
              We help B2B marketing teams turn outdated, underperforming
              websites into scalable, high-converting products that engage
              audiences, generate pipeline, and accelerate growth.
            </p>
          </div>
          <div className="lower"></div>
        </div>

        <div className="w-full pt-[200px]">
          <ServiceCard />
        </div>
      </section>
    </Fragment>
  );
}
